*,
::after,
::before {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  line-height: 1.5;
}

.list-style {
  list-style: none;
}

.form {
  background-color: silver;
}

.form-item {
  text-align: center;
  box-sizing: border-box;
  padding: 20px;
  margin-top: 100px;
  margin-left: auto;
  margin-right: auto;
  position: static;
}

.form-label {
  display: block;
  font-size: 30px;
  line-height: 1.16;
  margin-top: 10px;
}

.form-input {
  padding: 25px;
  width: 350px;
  height: 50px;
  border-radius: 5px;
}

.wrapper {
  text-align: center;
}

.form-list {
  display: block;
  margin-left: auto;
  padding-left: auto;
}

.form-ul {
  list-style: none;
  padding-left: 270px;
}

.list-style--languages{
  display: flex;
  justify-content: center;
}

.list-style--languages ul{
padding-left: 5px;
}